import React, { useState, } from 'react';
import { Button, Form, Input, Popconfirm, Table, Drawer } from 'antd';

const EditableRow = ({ index, ...props }) => {
  const [form] = Form.useForm();
  return ( 
    <Form form={form} component={false}>
        <tr {...props} />
    </Form>
  );
};
const EditableCell = ({
  title,  
  editable,
  children,
  dataIndex,
  record,
  handleSave,
  ...restProps
}) => {
  const [editing] = useState(false);
  
  let childNode = children;
  if (editable) {
    childNode = editing ? (
     <>
     </>
    ) : (
      <div>
      </div>
    );
  }
  return <td {...restProps}>{childNode}</td>;
};
const App = () => {
  const [drawerData, setDrawerData] = useState({});
  const [open, setOpen] = useState(false);

  const [searchText,setSearchText] = useState('')
  const [count, setCount] = useState(1);

  const [dataSource, setDataSource] = useState([]);

  const [drawerTitle,setDrawerTitle] = useState('')
  const [form] = Form.useForm();

  const { Search } = Input;
  const onSearch = (value, _e, info) => {
    console.log(info?.source, value);
    setSearchText(value)
  }
  // filter 
  const filterData = dataSource.filter(item => 
    item.name.includes(searchText) || item.address.includes(searchText)
    );

  const handleEdit = (key) => {
    setOpen(true);
    const index = dataSource.findIndex((item) => item.key === key);
    if (index !== -1) {
      dataSource[index].editing = true;
      setDrawerData(dataSource[index]);
      setDrawerTitle('')
      setDataSource([...dataSource]); 
      form.setFieldsValue(dataSource[index])  
    }
  };

  //
  const handleDelete = (key) => {
    const newData = dataSource.filter((item) => item.key !== key);
    setDataSource(newData);
  };

  const defaultColumns = [
    {
      title: '',
      dataIndex: 'number',
      width: '25%',
      //
      render: (_, __, index) => index + 1,
    },
    {
      title: '',
      dataIndex: 'name',
      width: '25%',
    },
    {
      title: '',
      dataIndex: 'address',
      width: '25%',
    },
    {
      title: '',
      dataIndex: 'operation', 
      width: '25%',
      //
      render: (_, record) =>(
        <div>
          <Button style={{marginRight:'5px'}} onClick={() => handleEdit(record.key)} ></Button>
          <Popconfirm title="?" onConfirm={() => handleDelete(record.key)}>
            <Button danger></Button>
          </Popconfirm>
        </div>
      ),
    },
  ];
  const handleAdd = () =>{
    setOpen(true);
    setDrawerData({});
    form.resetFields();  // 
  }

  const handleSave = (row) => {
    const newData = [...dataSource];
    const index = newData.findIndex((item) => row.key === item.key);
    if (index !== -1) {
      const item = newData[index];
      newData.splice(index, 1, {
        ...item,
        ...row,
      });
    } else {
      newData.push({
        key: count,
        ...row,
      });
      setCount(count + 1);
    }
    setDataSource(newData);
  };
  // defaultColumns  columns 
  const components = {
    body: {
      row: EditableRow,
      cell: EditableCell,
    },
  };

  //
  const columns = defaultColumns.map((col) => {
    if (!col.editable) {
      return col;
    }
    return {
      ...col,
      onCell: (record) => ({
        record,
        editable: col.editable,
        dataIndex: col.dataIndex,
        title: col.title,
        handleSave,
      }),
    };
  });


  return (
    <div style={{padding:'20px'}}>
      <Button
        onClick={handleAdd}
        type="primary"
        style={{
          marginBottom: 16,marginRight: '20px'
        }}
      >
        
      </Button>

      <Search
        placeholder=""
        onSearch={onSearch}
        style={{
          width: 200,
        }}
      /> 
      <Table
        components={components}  
        rowClassName={() => 'editable-row'}  
        bordered    //
        //
        dataSource={filterData}
        columns={columns}
      />
      <Drawer
        title={drawerTitle} // drawerTitle
        placement="right" 
        closable={true}   
        onClose={() => {
          setOpen(false)
          setDrawerTitle('')
        }}
        open={open}
        width={400}
      >
        <Form
          form={form}
          onFinish={(values) => {
            //
            const mergedData = { ...drawerData, ...values };
            handleSave(mergedData);
            setOpen(false);
            setDrawerTitle('')
          }}
          initialValues={drawerData}
        >
          <Form.Item label="" name="name" rules={[{ required: true, message: '' }]}>
            <Input />
          </Form.Item>

          <Form.Item label="" name="address" rules={[{ required: true, message: '' }]}>
            <Input />
          </Form.Item>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form>
      </Drawer>
    </div>
  );
};
export default App;